import React from 'react';

import { useEffect, useState, useContext } from 'react';

import { Link, useParams } from "react-router-dom";


import { useNavigate } from "react-router-dom";


import noteContext from './NoteContext';

import API from "./API";
import history from "history/browser";



export default function CommodityGradeBlock() {


    const navigate = useNavigate();

    const a = useContext(noteContext);

    history.listen(({ action, location }) => {

        navigate("/")
    });



    function sendto(provider) {

        setHideLoading(false);

        window.location.replace("https://api.whatsapp.com/send?phone=917300042331&test=https://saudebazi.com/");



    }


    function check_user_role(text, price, price_on) {

        return display_price(text, price, price_on);
        /*

        keep this code because their is a require to build a page which will be used for the google 
        if (a.subscription_id !== null)
            return display_price(text, price, price_on);
        else
            return (
                <>
                    <div className='saksh_rate'>
                        <strong>{text}</strong> <br />     Hidden
                    </div>
                    <div className='saksh_price_on'>
                        {price_on}
                    </div>
                </>

            );

*/
    }



    function display_price(text, price, price_on) {





        if (price === "0") return (<>  </>);




        if (price !== "") {
            return (
                <>
                    <div className='saksh_rate'>
                        <strong>{text}</strong> <br />  â‚¹ {price}/Kg
                    </div>
                    <div className='saksh_price_on'>
                        {price_on}
                    </div>
                </>

            );
        }
        else {
            return (
                <>
                    <div className='saksh_rate'>
                        <strong>{text}</strong> <br />      Contact Support for <br />  the latest rate
                    </div>
                    <div className='saksh_price_on'>

                    </div>
                </>

            );

        }




    }


    function display_array(price, change_type, change_percentage) {









        if (price === "0") return (<>

        </>);



        else



            return (
                <>
                    <div className='saksh_rate'>
                        <strong>Change % </strong> <br />
                    </div>
                    <div className='saksh_price_on'>

                        {(change_type === 'green') ? (



                            <i >  <div className="green"> {change_percentage} &uarr; </div></i>
                        ) : (
                            <i >  <div className='red'>  {change_percentage} &darr; </div></i>
                        )}
                    </div>
                </>

            );









    }



    const { grade } = useParams();

    let { label } = useParams();

    const [index, setData_index] = useState(2);

    const [data, setData] = useState([]);
    const [message, setMessage] = useState();
    const [grade_name, setGrade] = useState();
    
    const [loadingbar, setHideLoading] = useState(false);

    const dt = useContext(noteContext);


    let token = dt.token;

    const fetchData = async (label, grade, token) => {


        



        const res = await API.get("auth/commodity/" + label + "/" + grade, {
            headers: {
                Accept: 'application/json',
                Authorization: 'Bearer ' + token
            }
        });

        setHideLoading(true);
        setData(res.data.rates_array);

        setGrade(res.data.grade);


        if (res.data.length === 0)
            setMessage("No deals available for this grade.");
        else
            setMessage();



    };




    useEffect(() => {

        console.log(label + grade);

        setHideLoading(false);


        fetchData(label, grade, token);



    }, [label, grade, token]);


    if (!loadingbar)


        return (


            "Loading... please wait "
        )

    return (
        <div  >
            {message}
            {grade_name}
            {data.map((item, index) => (



                <div key={index}>

                    <div className='price_block mb-5'>






                        <div className='row'>


                            <div className='col-md-3'>
                                <div className='market_name'>
                                    <strong>  {item.market} </strong>
                                </div>
                            </div>



                            <div className='col-md-2'>






                                {display_price("Last Rate", item.old_rate, item.old_price_on)}





                            </div>



                            <div className='col-md-2'>



                                {check_user_role("Latest Rate", item.rate, item.price_on)}

                            </div>


                            <div className='col-md-3'>
                                <div className="array_direction"    >
                                    {display_array(item.old_rate, item.change_type, item.change_percentage)}



                                </div>


                            </div>


                            <div className='col-md-2 align-middle sakshmiddlebtn'>




                                <button className="btn btn-outline-success"

                                    onClick={() => sendto(item.whatsapp_message_link)}  >
                                    Enquire now

                                </button>


                            </div>




                        </div>

                    </div>
                </div>
            ))}

        </div>
    );


};



